import React from 'react';
import { Grid, Table, TableHeaderRow } from '@devexpress/dx-react-grid-material-ui';
import {
    SortingState,
    IntegratedSorting,
} from '@devexpress/dx-react-grid';
import Paper from '@material-ui/core/Paper';

const prepareTags = repositories => repositories
      .flatMap(repo => repo.topics)
      .reduce((acc, el) => ({
          ...acc,
          [el]: acc[el] ? acc[el] + 1 : 1
      }), {});

export default (props) => {
  const tags = prepareTags(props.repositories);
  const data = Object.entries(tags).map(([key, value]) => ({
    topicName: key,
    topicCount: value
  }));
  console.log(data);
  return (<Paper>
    <Grid
      columns={[{
        name: 'topicName',
        title: 'Topic Name'
      },{
        name: 'topicCount',
        title: 'Topic Count'
      }]}
      rows={data}>
      <SortingState
        defaultSorting={[{ columnName: 'topicCount', direction: 'desc' }]}
      />
      <IntegratedSorting />
      <Table />
      <TableHeaderRow showSortingControls />
    </Grid>
  </Paper>);
}
